import { makeAutoObservable } from "mobx";

const orgFn = (selectedProps) => {
  let orgTariff = {};

  let provisoC1_2 =
    (selectedProps[""] === " " &&
      Number(selectedProps["  "]) +
        Number(selectedProps["  "]) <=
        15 &&
      selectedProps[" "] === "" &&
      selectedProps[""] === "0,4   ") ||
    (selectedProps[""] ===
      "    " &&
      Number(selectedProps["  "]) +
        Number(selectedProps["  "]) <=
        150 &&
      (selectedProps[" "] === "" ||
        selectedProps[" "] === "") &&
      selectedProps[""] === "0,4   ");

  let provisoMain = [
    " ",
    "",
    "",
    "   ",
    "  ",
    "  ",
    "    ",
  ].every((name) => selectedProps.hasOwnProperty(name));

  // 2025 
  orgTariff.c1_1 = 15463.93;
  orgTariff.c1_2 = provisoMain
    ? provisoC1_2
      ? {
          name: "       ",
          value: 7095.65,
          number: "1.2.1.",
        }
      : {
          name: "    ",
          value: 25721.75,
          number: "1.2.2.",
        }
    : 0;

  return orgTariff;
};

const constructionBenefits = (selectedProps) => {
  let individualBenefit =
    selectedProps[""] === " " &&
    Number(selectedProps["  "]) +
      Number(selectedProps["  "]) <=
      15 &&
    selectedProps[" "] === "" &&
    selectedProps[""] === "0,4   " &&
    (selectedProps["   "] ===
      " 200  " ||
      selectedProps["   "] ===
        " 200  300  ");

  let entityBenefits =
    selectedProps[""] ===
      "    " &&
    Number(selectedProps["  "]) +
      Number(selectedProps["  "]) <=
      150 &&
    (selectedProps[" "] === "" ||
      selectedProps[" "] === "") &&
    selectedProps["   "] ===
      " 200  " &&
    selectedProps[""] === "0,4   ";

  return individualBenefit
    ? "individual benefit"
    : entityBenefits
    ? "entity benefit"
    : false;
};

export default class ConditionsStore {
  constructor() {
    this.props = [
      { name: " ", value: ["", "", ""] },
      {
        name: "",
        value: ["0,4   ", "6-10 ", "20 ", " 20 "],
      },
      {
        name: "",
        value: [
          "    ",
          " ",
        ],
      },
      {
        name: "   ",
        value: [
          " 200  ",
          " 200  300  ",
          " 300 ",
        ],
      },
      { name: "  ", value: 0 },
      { name: "  ", value: 0 },
      {
        name: "    ",
        value: ["  ", "2 ", "3 ", "4 "],
      },
    ];
    this.paperTariff = {
      // 2025 
      c1_1: 15463.93,
      c1_2: { name: "", value: 0, number: "" },
    };
    this.selectedProps = {};
    this.indexes = {
      2026: 1.042,
      2027: 1.04,
      2028: 1.0,
      2029: 1.0,
    };
    this.periodI = true;

    this.indexing = () => {
      const two = 0.5 + 0.5 * this.indexes["2026"];
      const three =
        (this.periodI &&
          0.5 * this.indexes["2026"] * this.indexes["2027"] +
            0.5 *
              this.indexes["2026"] *
              this.indexes["2027"] *
              this.indexes["2028"]) ||
        0.5 * this.indexes["2026"] +
          0.5 *
            this.indexes["2026"] *
            this.indexes["2027"] *
            this.indexes["2028"];
      const four =
        0.5 * this.indexes["2026"] * this.indexes["2027"] +
        0.5 * this.indexes["2028"] * this.indexes["2029"];
      const obj = {
        "  ": 1,
        "2 ": two,
        "3 ": three,
        "4 ": four,
      };
      return (
        obj[this.selectedProps["    "]] || 1
      ); /*undefined  */
    };

    makeAutoObservable(this);
  }

  setProperty(propName, propValue) {
    this.selectedProps[propName] = propValue;
    this.paperTariff = orgFn(this.selectedProps);
    this.constructionBenefits = constructionBenefits(this.selectedProps);
  }

  setIndex(index, value) {
    this.indexes[index] = value;
  }

  changePeriod() {
    this.periodI = !this.periodI;
  }

  check(propName) {
    return this.selectedProps[propName];
  }

  setIndexes(newIndexes) {
    this.indexes = newIndexes;
  }

  get index() {
    return this.indexing();
  }

  get reliability() {
    return this.selectedProps[" "];
  }

  get voltage() {
    return this.selectedProps[""];
  }

  get declarant() {
    return this.selectedProps[""];
  }

  get distance() {
    return this.selectedProps["   "];
  }

  get connectingCapacity() {
    return Number(this.selectedProps["  "]);
  }

  get availableCapacity() {
    return Number(this.selectedProps["  "]);
  }
}
